import React from "react";
import { NavLink } from "react-router-dom";


const Footer = ({topics}) => {
  return (
    <div className="container-fluid mt-3 py-4 px-8 footer">
      <div className="mx-md-5 px-md-5 text-white">
        <div className="row">
          
          <div className="col-sm-6 col-md-3">
            <div className="mb-3 footer-heading-text">
              
              <div>
              <NavLink to="#" className="footer-links">
                 Get in touch!
              </NavLink>
            </div>
            </div>
           
          </div>
          <div className="col-md-3 col-sm-6">
          <div>
              <NavLink to="#" className="footer-links">
                Location
              </NavLink>
            </div>
            <div>
              <NavLink to="#" className="footer-links">
                Bengaluru, Karnataka, India
              </NavLink>
            </div> 
          </div>
          <div className="col-md-3 col-sm-6 pb-5">
          <div>
              <NavLink to="#" className="footer-links">
                Contact
              </NavLink>
            </div>
            <div>
              <NavLink to="#" className="footer-links">
                support@pretva.com
              </NavLink>
            </div> 
          </div>
          <div className="col-md-3 col-sm-6 pb-5">
          <div>
              <NavLink to="#" className="footer-links">
                Follow
              </NavLink>
            </div>
            <div>
              <NavLink to="#" className="footer-links">
                support@pretva.com
              </NavLink>
            </div> 
          </div>
      </div>
      </div>
      </div>
  );
};
export default Footer;
