import React, { useState } from "react";
import Base from "../Base";
import { Link } from "react-router-dom";
import { signup } from "../Helper/coreapicalls";
import Img from '../photo.png'

const Signup = () => {
    const [values, setValues] = useState({
        fname: "",
        lname:"",
        type:"",
        companyName:"",
        country:"",
        ContactNo:"",
        email: "",
        password: "",
        error: "",
        success: false
      });
    
      const { fname,lname,type,companyName,country,ContactNo, email, password, error, success } = values;

  const handleChange = value => event => {
    setValues({ ...values, error: false, [value]: event.target.value });
  };

  const onSubmit = event => {
    event.preventDefault();
    setValues({ ...values, error: false });
    signup({ fname,lname,type,companyName,country,ContactNo, email, password })
      .then(data => {
        if (data.error) {
          setValues({ ...values, error: data.error, success: false });
        } else {
          setValues({
            ...values,
            fname: "",
            lname:"",
            type:"",
            companyName:"",
            country:"",
            ContactNo:"",
            email: "", 
            password: "",
            error: "",
            success: true  
          });
        }
      })
      .catch(console.log("Error in signup"));
  };

  const signUpForm = () => {
    return (
      <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          <form>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("fname")}
                type="text"
                value={fname}
                placeholder="First Name"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("lname")}
                type="text"
                value={lname}
                placeholder="Last Name"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("type")}
                type="text"
                value={type}
                placeholder="Are you a Buyer or Supplier"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("companyName")}
                type="text"
                value={companyName}
                placeholder="Company Name"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("country")}
                type="text"
                value={country}
                placeholder="Country"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("ContactNo")}
                type="text"
                value={ContactNo}
                placeholder="Contact Number"
              />
            </div>
            <div className="form-group">
              <input
                className="form-control"
                onChange={handleChange("email")}
                type="email"
                value={email}
                placeholder="Email Address"
              />
            </div>

            <div className="form-group">
              <input
                onChange={handleChange("password")}
                className="form-control"
                type="password"
                value={password}
                placeholder="Choose Password"
              />
            </div>
            <button onClick={onSubmit} className="btn btn-success btn-block">
              Submit
            </button>
          </form>
        </div>
      </div>
    );
  };

  const successMessage = () => {
    return (
      <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          <div
            className="alert alert-success"
            style={{ display: success ? "" : "none" }}
          >
            New account was created successfully. Please
            <Link to="/login">Login Here</Link>
          </div>
        </div>
      </div>
    );
  };

  const errorMessage = () => {
    return (
      <div className="row">
        <div className="col-md-6 offset-sm-3 text-left">
          <div
            className="alert alert-danger"
            style={{ display: error ? "" : "none" }}
          >
            {error}
          </div>
        </div>
      </div>
    );  
  };

  return (
    <Base>
    <div className="row">
      <div className="col-6">
        <img src={Img} height="100%" width="100%"/>
      </div>
      <div className="col-6">
        <h1 className="p-5 text-center">New User Registration</h1>
          {successMessage()}
          {errorMessage()}
          {signUpForm()}
      </div>
    </div>
  </Base>
  );
};

export default Signup;
